<template>
	<div id="deliveryer-index">
		<public-header title="配送员管理"></public-header>
		<div class="content">
			<div class="group">
				<van-row gutter="20">
					<van-col span="8" v-if="util.checkperm('deliveryer.plateform')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/deliveryer/deliveryer'})" class="deliveryer-item">
							<div class="top">
								<i class="icon icon-friend c-primary"></i>
							</div>
							<div class="bottom">
								<span>配送员列表</span>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkperm('deliveryer.getcash')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/deliveryer/getcash'})" class="deliveryer-item">
							<div class="deliveryer-item">
								<div class="top">
									<i class="icon icon-sponsor c-info"></i>
								</div>
								<div class="bottom">
									<span>提现申请</span>
								</div>
							</div>
						</router-link>
					</van-col>
					<van-col span="8" v-if="util.checkperm('deliveryer.current')">
						<router-link tag="div" :to="util.getUrl({path: '/pages/deliveryer/current'})" class="deliveryer-item">
							<div class="deliveryer-item">
								<div class="top">
									<i class="icon icon-order c-warning"></i>
								</div>
								<div class="bottom">
									<span>账户明细</span>
								</div>
							</div>
						</router-link>
					</van-col>
					<van-col span="8">
						<router-link tag="div" :to="util.getUrl({path: '/pages/deliveryer/location'})" class="deliveryer-item">
							<div class="deliveryer-item">
								<div class="top">
									<i class="icon icon-location c-danger"></i>
								</div>
								<div class="bottom">
									<span>配送员位置</span>
								</div>
							</div>
						</router-link>
					</van-col>
				</van-row>
			</div>
		</div>
	</div>

</template>

<script>
	import publicHeader from "@/components/header"
	export default {
		data() {
			return {

			}
		},
		components: {
			publicHeader,
		},
		methods: {

		}
	}
</script>

<style>
	#deliveryer-index .content{
		background-color: #fff;
	}
	#deliveryer-index .content .group{
		padding: 20px 15px;
	}
	#deliveryer-index .content .group .van-row{
		text-align: center;
	}
	#deliveryer-index .content .group .deliveryer-item{
		margin-bottom: 20px;
	}
	#deliveryer-index .content .group .deliveryer-item .top{
		padding: 10px;
	}
	#deliveryer-index .content .group .deliveryer-item .top .icon{
		font-size: 40px;
	}
	#deliveryer-index .content .group .deliveryer-item .bottom{
		font-size: 14px;
		color: #7D8484;
	}
</style>


